import { handlePrice } from '../utils/utils';
import { Component } from './base/Component';
/*
 * Класс, описывающий корзину товаров
 * */
export class Basket extends Component {
    // Конструктор принимает имя блока, родительский элемент и обработчик событий
    constructor(blockName, container, events) {
        super(container);
        this.blockName = blockName;
        this.events = events;
        this._button = container.querySelector(`.${blockName}__button`);
        this._price = container.querySelector(`.${blockName}__price`);
        this._list = container.querySelector(`.${blockName}__list`);
        if (this._button) {
            this._button.addEventListener('click', () => this.events.emit('basket:order'));
        }
    }
    // Сеттер для общей цены
    set price(price) {
        this.setText(this._price, handlePrice(price) + ' синапсов');
    }
    // Сеттер для списка товаров
    set list(items) {
        this._list.replaceChildren(...items);
        this.setDisabled(this._button, items.length === 0);
    }
    // Метод отключающий кнопку "Оформить"
    disableButton() {
        this.setDisabled(this._button, true);
    }
    // Сделаю публичный метод для получения списка элементов по другому не выйдет.
    getList() {
        return this._list;
    }
}
export class StoreItemBasket extends Component {
    constructor(blockName, container, actions) {
        super(container);
        this.blockName = blockName;
        this._title = container.querySelector(`.${blockName}__title`);
        this._index = container.querySelector(`.basket__item-index`);
        this._price = container.querySelector(`.${blockName}__price`);
        this._button = container.querySelector(`.${blockName}__button`);
        if (this._button) {
            this._button.addEventListener('click', (evt) => {
                this.container.remove();
                actions === null || actions === void 0 ? void 0 : actions.onClick(evt);
            });
        }
    }
    set title(value) {
        this.setText(this._title, value);
    }
    set index(value) {
        this.setText(this._index, value.toString());
    }
    set price(value) {
        this.setText(this._price, handlePrice(value) + ' синапсов');
    }
}
